import { Steps, WorkflowDescriptionResult } from "./workflow.types"

type RestrictedSteps = Partial<Record<Steps["stepType"], boolean>>

const filterWorkflowSteps = (
  workflow: WorkflowDescriptionResult,
  restrictedSteps: RestrictedSteps,
): WorkflowDescriptionResult => ({
  ...workflow,
  steps: workflow.steps?.filter(step => !restrictedSteps[step.stepType]),
})

export { filterWorkflowSteps }
